import React, { Component } from 'react'
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import axios from 'axios';

export default class SideBar extends Component {
    constructor(props) {
        super(props);
        this.state = { courses: [] }
    }

    componentDidMount() {
        axios.get('http://localhost:5000/user')
            .then(res => {
                this.setState({ courses: res.courses });
            })
            .catch((err) => console.log("Error: " + err));
    }

    courseList() {
        return this.state.courses.map(curr => {
            return (
                <div>
                    <ExpansionPanel style={{ borderRadius: 0 }}>
                        <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                            <span className="side-bar-courses-title">{curr}</span>
                        </ExpansionPanelSummary>
                        <div className="side-bar-lectures">
                            <div className="side-bar-lectures-title">
                                Lectures
                            </div>
                            {curr.lectures.map(lecture => {
                                return (
                                    <div className="side-bar-lecture">
                                        <span>{lecture}</span>
                                    </div>
                                )
                            })}
                        </div>
                    </ExpansionPanel>
                </div>
            );
        })
    }

    render() {
        return (
        <div className="side-bar">
            <div className="side-bar-title">Courses:</div>
            {this.courseList()}
        </div>
        )
    }
}

